'use client';

import React, { useState } from 'react';
import { DeviceFolder, NetworkDevice } from '@/types/schemas';
import {
  ChevronRight,
  ChevronDown,
  Folder,
  FolderOpen,
  Plus,
  Edit,
  Trash2,
  UserPlus,
  Network,
  Server,
  Monitor,
  Printer,
  Smartphone,
  Globe,
  Database,
  Building,
  Shield,
} from 'lucide-react';

interface FolderTreeProps {
  folders: DeviceFolder[];
  devices: NetworkDevice[];
  selectedFolderId?: string;
  rootFolderExpanded: boolean;
  othersExpanded: boolean;
  onFolderSelect: (folderId: string) => void;
  onFolderToggle: (folderId: string) => void;
  onAddFolder: (parentId: string) => void;
  onEditFolder: (folderId: string) => void;
  onDeleteFolder: (folderId: string) => void;
  onAddDeviceToFolder: (folderId: string) => void;
}

const iconMap = {
  Network,
  Server,
  Monitor,
  Printer,
  Smartphone,
  Globe,
  Database,
  Building,
  Folder,
  Shield,
};

export function FolderTree({
  folders,
  devices,
  selectedFolderId,
  rootFolderExpanded,
  othersExpanded,
  onFolderSelect,
  onFolderToggle,
  onAddFolder,
  onEditFolder,
  onDeleteFolder,
  onAddDeviceToFolder,
}: FolderTreeProps) {
  const getDeviceCount = (folderId: string): number => {
    if (folderId === 'root') {
      return devices.length; // –î–ª—è –∫–æ—Ä–Ω–µ–≤–æ–π –ø–∞–ø–∫–∏ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –≤—Å–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
    }
    return devices.filter((device) => device.folderId === folderId).length;
  };

  const getOnlineDeviceCount = (folderId: string): number => {
    if (folderId === 'root') {
      return devices.filter((device) => device.status === 'online').length;
    }
    return devices.filter(
      (device) => device.folderId === folderId && device.status === 'online'
    ).length;
  };

  // –§—É–Ω–∫—Ü–∏—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ –∫–ª–∏–∫–∞ –Ω–∞ –ø–∞–ø–∫—É
  const handleFolderClick = (folder: DeviceFolder) => {
    const hasChildren = folder.children && folder.children.length > 0;
    const folderDevices = devices.filter(
      (device) => device.folderId === folder.id
    );
    const hasDevices = folderDevices.length > 0;
    const shouldExpand = hasChildren || hasDevices;

    if (selectedFolderId === folder.id) {
      // –ï—Å–ª–∏ –ø–∞–ø–∫–∞ —É–∂–µ –≤—ã–±—Ä–∞–Ω–∞, —Ç–æ–ª—å–∫–æ –ø–µ—Ä–µ–∫–ª—é—á–∞–µ–º –µ—ë —Å–æ—Å—Ç–æ—è–Ω–∏–µ (—Ä–∞–∑–≤–µ—Ä–Ω—É—Ç—å/—Å–≤–µ—Ä–Ω—É—Ç—å)
      if (shouldExpand) {
        onFolderToggle(folder.id);
      }
    } else {
      // –ï—Å–ª–∏ –ø–∞–ø–∫–∞ –Ω–µ –≤—ã–±—Ä–∞–Ω–∞, –≤—ã–±–∏—Ä–∞–µ–º –µ—ë –∏ —Ä–∞–∑–≤–æ—Ä–∞—á–∏–≤–∞–µ–º (–µ—Å–ª–∏ –µ—Å—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ)
      onFolderSelect(folder.id);
      if (shouldExpand && !folder.expanded) {
        onFolderToggle(folder.id);
      }
    }
  };

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
  const renderDevice = (device: NetworkDevice, level: number = 0) => {
    return (
      <div
        key={device.id}
        className="select-none"
        draggable
        onDragStart={(e) => {
          e.dataTransfer.setData('text/plain', device.id);
          e.dataTransfer.setData('device-id', device.id);
        }}
      >
        <div
          className={`group flex cursor-pointer items-center justify-between rounded-lg p-2 transition-all duration-200 ${
            selectedFolderId === device.id
              ? 'border border-green-500/30 bg-green-500/20'
              : 'hover:bg-slate-700/50'
          }`}
          style={{ paddingLeft: `${level * 20 + 28}px` }}
          onClick={() => {
            console.log('Device clicked:', device.id, device.name);
            onFolderSelect(device.id);
          }}
        >
          <div className="flex flex-1 items-center space-x-2">
            <div
              className="rounded-lg border p-1.5"
              style={{
                backgroundColor:
                  device.status === 'online'
                    ? '#10B98120'
                    : device.status === 'warning'
                      ? '#F59E0B20'
                      : '#EF444420',
                borderColor:
                  device.status === 'online'
                    ? '#10B98140'
                    : device.status === 'warning'
                      ? '#F59E0B40'
                      : '#EF444440',
                color:
                  device.status === 'online'
                    ? '#10B981'
                    : device.status === 'warning'
                      ? '#F59E0B'
                      : '#EF4444',
              }}
            >
              {(() => {
                const deviceIcons = {
                  router: Network,
                  switch: Network,
                  server: Server,
                  workstation: Monitor,
                  printer: Printer,
                  mobile: Smartphone,
                  firewall: Shield,
                  access_point: Globe,
                };
                const DeviceIcon =
                  deviceIcons[device.type as keyof typeof deviceIcons] ||
                  Monitor;
                return <DeviceIcon className="h-4 w-4" />;
              })()}
            </div>

            <div className="flex-1">
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-white">
                  {device.name}
                </span>
                <div className="flex items-center space-x-1 text-xs">
                  <span
                    className={
                      device.status === 'online'
                        ? 'text-green-400'
                        : device.status === 'warning'
                          ? 'text-yellow-400'
                          : 'text-red-400'
                    }
                  >
                    {device.status}
                  </span>
                </div>
              </div>
              <p className="truncate text-xs text-slate-500">{device.ip}</p>
            </div>
          </div>

          <div className="flex items-center space-x-1 opacity-0 transition-opacity group-hover:opacity-100">
            <button
              onClick={(e) => {
                e.stopPropagation();
                console.log('Edit device:', device.id);
              }}
              className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-white"
              title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ"
            >
              <Edit className="h-3 w-3" />
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                console.log('Delete device:', device.id);
              }}
              className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-red-400"
              title="–£–¥–∞–ª–∏—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ"
            >
              <Trash2 className="h-3 w-3" />
            </button>
          </div>
        </div>
      </div>
    );
  };

  const renderFolder = (folder: DeviceFolder, level: number = 0) => {
    const IconComponent =
      iconMap[folder.icon as keyof typeof iconMap] || Folder;
    const deviceCount = getDeviceCount(folder.id);
    const onlineCount = getOnlineDeviceCount(folder.id);
    const hasChildren = folder.children && folder.children.length > 0;
    const folderDevices = devices.filter(
      (device) => device.folderId === folder.id
    );
    const hasDevices = folderDevices.length > 0;
    const shouldShowExpandButton = hasChildren || hasDevices;

    return (
      <div key={folder.id} className="select-none">
        <div
          className={`group flex cursor-pointer items-center justify-between rounded-lg p-2 transition-all duration-200 ${
            selectedFolderId === folder.id
              ? 'border border-blue-500/30 bg-blue-500/20'
              : 'hover:bg-slate-700/50'
          }`}
          style={{ paddingLeft: `${level * 20 + 8}px` }}
          onClick={() => {
            console.log('Folder clicked:', folder.id, folder.name);
            handleFolderClick(folder);
          }}
          onDragOver={(e) => {
            e.preventDefault();
            e.currentTarget.classList.add('bg-blue-500/10');
          }}
          onDragLeave={(e) => {
            e.currentTarget.classList.remove('bg-blue-500/10');
          }}
          onDrop={(e) => {
            e.preventDefault();
            e.currentTarget.classList.remove('bg-blue-500/10');
            const deviceId = e.dataTransfer.getData('device-id');
            if (deviceId) {
              console.log('Moving device', deviceId, 'to folder', folder.id);
              // TODO: Implement device move logic
            }
          }}
        >
          <div className="flex flex-1 items-center space-x-2">
            {shouldShowExpandButton && (
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  console.log(
                    'Toggle folder:',
                    folder.id,
                    'current expanded:',
                    folder.expanded
                  );
                  onFolderToggle(folder.id);
                }}
                className="rounded p-1 hover:bg-slate-600/50"
              >
                {folder.expanded ? (
                  <ChevronDown className="h-4 w-4 text-slate-400" />
                ) : (
                  <ChevronRight className="h-4 w-4 text-slate-400" />
                )}
              </button>
            )}

            <div
              className="rounded-lg border p-1.5"
              style={{
                backgroundColor: `${folder.color}20`,
                borderColor: `${folder.color}40`,
                color: folder.color,
              }}
            >
              {folder.expanded && shouldShowExpandButton ? (
                <FolderOpen className="h-4 w-4" />
              ) : (
                <IconComponent className="h-4 w-4" />
              )}
            </div>

            <div className="flex-1">
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-white">
                  {folder.name}
                </span>
                {deviceCount > 0 && (
                  <div className="flex items-center space-x-1 text-xs">
                    <span className="text-green-400">{onlineCount}</span>
                    <span className="text-slate-500">/</span>
                    <span className="text-slate-400">{deviceCount}</span>
                  </div>
                )}
              </div>
              {folder.description && (
                <p className="truncate text-xs text-slate-500">
                  {folder.description}
                </p>
              )}
            </div>
          </div>

          {/* –í—Å–µ–≥–¥–∞ –≤–∏–¥–∏–º—ã–µ –∫–Ω–æ–ø–∫–∏ –¥–µ–π—Å—Ç–≤–∏–π */}
          <div className="flex items-center space-x-1 opacity-0 transition-opacity group-hover:opacity-100">
            <button
              onClick={(e) => {
                e.stopPropagation();
                onAddDeviceToFolder(folder.id);
              }}
              className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-green-400"
              title="–î–æ–±–∞–≤–∏—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –≤ —ç—Ç—É –ø–∞–ø–∫—É"
            >
              <Monitor className="h-3 w-3" />
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                onAddFolder(folder.id);
              }}
              className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-blue-400"
              title="–î–æ–±–∞–≤–∏—Ç—å –ø–æ–¥–ø–∞–ø–∫—É"
            >
              <Plus className="h-3 w-3" />
            </button>
            {folder.id !== 'root' && (
              <>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    onEditFolder(folder.id);
                  }}
                  className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-white"
                  title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø–∞–ø–∫—É"
                >
                  <Edit className="h-3 w-3" />
                </button>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    onDeleteFolder(folder.id);
                  }}
                  className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-red-400"
                  title="–£–¥–∞–ª–∏—Ç—å –ø–∞–ø–∫—É"
                >
                  <Trash2 className="h-3 w-3" />
                </button>
              </>
            )}
          </div>
        </div>

        {folder.expanded && (
          <div className="mt-1">
            {/* –ü–æ–¥–ø–∞–ø–∫–∏ */}
            {hasChildren &&
              folder.children.map((child: DeviceFolder) =>
                renderFolder(child, level + 1)
              )}

            {/* –£—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ –≤ —ç—Ç–æ–π –ø–∞–ø–∫–µ */}
            {hasDevices &&
              folderDevices.map((device) => renderDevice(device, level + 1))}
          </div>
        )}
      </div>
    );
  };

  // –°–æ–∑–¥–∞–µ–º –≤–∏—Ä—Ç—É–∞–ª—å–Ω—É—é –∫–æ—Ä–Ω–µ–≤—É—é –ø–∞–ø–∫—É
  const rootFolder: DeviceFolder = {
    id: 'root',
    name: '–í—Å–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞',
    description: '–í—Å–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —Å–µ—Ç–∏',
    parentId: undefined,
    color: '#3B82F6',
    icon: 'Network',
    expanded: rootFolderExpanded,
    children: folders,
  };

  const totalDevices = devices.length;
  const totalOnline = devices.filter((d) => d.status === 'online').length;

  return (
    <div className="space-y-1">
      {/* –ö–æ—Ä–Ω–µ–≤–∞—è –ø–∞–ø–∫–∞ "–í—Å–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞" */}
      <div className="select-none">
        <div
          className={`group flex cursor-pointer items-center justify-between rounded-lg p-2 transition-all duration-200 ${
            selectedFolderId === 'root' || !selectedFolderId
              ? 'border border-blue-500/30 bg-blue-500/20'
              : 'hover:bg-slate-700/50'
          }`}
          onClick={() => {
            console.log('Root folder clicked');
            handleFolderClick(rootFolder);
          }}
        >
          <div className="flex flex-1 items-center space-x-2">
            <button
              onClick={(e) => {
                e.stopPropagation();
                onFolderToggle('root');
              }}
              className="rounded p-1 hover:bg-slate-600/50"
            >
              {rootFolder.expanded ? (
                <ChevronDown className="h-4 w-4 text-slate-400" />
              ) : (
                <ChevronRight className="h-4 w-4 text-slate-400" />
              )}
            </button>

            <div
              className="rounded-lg border p-1.5"
              style={{
                backgroundColor: '#3B82F620',
                borderColor: '#3B82F640',
                color: '#3B82F6',
              }}
            >
              {rootFolder.expanded ? (
                <FolderOpen className="h-4 w-4" />
              ) : (
                <Network className="h-4 w-4" />
              )}
            </div>

            <div className="flex-1">
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-white">
                  –í—Å–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
                </span>
                <div className="flex items-center space-x-1 text-xs">
                  <span className="text-green-400">{totalOnline}</span>
                  <span className="text-slate-500">/</span>
                  <span className="text-slate-400">{totalDevices}</span>
                </div>
              </div>
              <p className="truncate text-xs text-slate-500">
                –í—Å–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —Å–µ—Ç–∏
              </p>
            </div>
          </div>

          <div className="flex items-center space-x-1 opacity-0 transition-opacity group-hover:opacity-100">
            <button
              onClick={(e) => {
                e.stopPropagation();
                onAddDeviceToFolder('root');
              }}
              className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-green-400"
              title="–î–æ–±–∞–≤–∏—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –≤ –∫–æ—Ä–µ–Ω—å"
            >
              <Monitor className="h-3 w-3" />
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                onAddFolder('root');
              }}
              className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-blue-400"
              title="–î–æ–±–∞–≤–∏—Ç—å –ø–∞–ø–∫—É"
            >
              <Plus className="h-3 w-3" />
            </button>
          </div>
        </div>

        {/* –í–ª–æ–∂–µ–Ω–Ω—ã–µ –ø–∞–ø–∫–∏ –∏ –ø–∞–ø–∫–∞ "–ò–Ω—ã–µ" */}
        {rootFolder.expanded && (
          <div className="mt-1">
            {folders.map((folder) => renderFolder(folder, 1))}

            {/* –ü–∞–ø–∫–∞ "–ò–Ω—ã–µ" –¥–ª—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤ –±–µ–∑ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ */}
            {(() => {
              const uncategorizedDevices = devices.filter(
                (device) => !device.folderId || device.folderId === 'root'
              );

              if (uncategorizedDevices.length === 0) return null;

              const othersFolder: DeviceFolder = {
                id: 'others',
                name: '–ò–Ω—ã–µ',
                description: '–£—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ –±–µ–∑ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏',
                parentId: 'root',
                color: '#6B7280',
                icon: 'Folder',
                expanded: othersExpanded,
                children: [],
              };

              return (
                <div key="others" className="select-none">
                  <div
                    className={`group flex cursor-pointer items-center justify-between rounded-lg p-2 transition-all duration-200 ${
                      selectedFolderId === 'others'
                        ? 'border border-blue-500/30 bg-blue-500/20'
                        : 'hover:bg-slate-700/50'
                    }`}
                    style={{ paddingLeft: '28px' }}
                    onClick={() => {
                      console.log('Others folder clicked');
                      onFolderSelect('others');
                      if (!othersFolder.expanded) {
                        onFolderToggle('others');
                      }
                    }}
                    onDragOver={(e) => {
                      e.preventDefault();
                      e.currentTarget.classList.add('bg-blue-500/10');
                    }}
                    onDragLeave={(e) => {
                      e.currentTarget.classList.remove('bg-blue-500/10');
                    }}
                    onDrop={(e) => {
                      e.preventDefault();
                      e.currentTarget.classList.remove('bg-blue-500/10');
                      const deviceId = e.dataTransfer.getData('device-id');
                      if (deviceId) {
                        console.log(
                          'Moving device',
                          deviceId,
                          'to others folder'
                        );
                        // TODO: Implement device move to others
                      }
                    }}
                  >
                    <div className="flex flex-1 items-center space-x-2">
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          onFolderToggle('others');
                        }}
                        className="rounded p-1 hover:bg-slate-600/50"
                      >
                        {othersFolder.expanded ? (
                          <ChevronDown className="h-4 w-4 text-slate-400" />
                        ) : (
                          <ChevronRight className="h-4 w-4 text-slate-400" />
                        )}
                      </button>

                      <div
                        className="rounded-lg border p-1.5"
                        style={{
                          backgroundColor: '#6B728020',
                          borderColor: '#6B728040',
                          color: '#6B7280',
                        }}
                      >
                        {othersFolder.expanded ? (
                          <FolderOpen className="h-4 w-4" />
                        ) : (
                          <Folder className="h-4 w-4" />
                        )}
                      </div>

                      <div className="flex-1">
                        <div className="flex items-center space-x-2">
                          <span className="text-sm font-medium text-white">
                            –ò–Ω—ã–µ
                          </span>
                          <div className="flex items-center space-x-1 text-xs">
                            <span className="text-green-400">
                              {
                                uncategorizedDevices.filter(
                                  (d) => d.status === 'online'
                                ).length
                              }
                            </span>
                            <span className="text-slate-500">/</span>
                            <span className="text-slate-400">
                              {uncategorizedDevices.length}
                            </span>
                          </div>
                        </div>
                        <p className="truncate text-xs text-slate-500">
                          –£—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ –±–µ–∑ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏
                        </p>
                      </div>
                    </div>

                    <div className="flex items-center space-x-1 opacity-0 transition-opacity group-hover:opacity-100">
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          onAddDeviceToFolder('others');
                        }}
                        className="rounded p-1 text-slate-400 hover:bg-slate-600/50 hover:text-green-400"
                        title="–î–æ–±–∞–≤–∏—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –≤ —ç—Ç—É –ø–∞–ø–∫—É"
                      >
                        <Monitor className="h-3 w-3" />
                      </button>
                    </div>
                  </div>

                  {/* –£—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ –≤ –ø–∞–ø–∫–µ "–ò–Ω—ã–µ" */}
                  {othersFolder.expanded && (
                    <div className="mt-1">
                      {uncategorizedDevices.map((device) =>
                        renderDevice(device, 2)
                      )}
                    </div>
                  )}
                </div>
              );
            })()}
          </div>
        )}
      </div>
    </div>
  );
}
