'use client';

import React, { useState, useEffect, useRef } from 'react';
import {
  Zap,
  Settings,
  Terminal,
  Wifi,
  AlertCircle,
  Trash2,
  Edit3,
  Activity,
  Globe,
  Shield,
  MoreHorizontal,
} from 'lucide-react';
import { NetworkDevice } from '@/types/network';

interface ContextMenuProps {
  device: NetworkDevice;
  position: { x: number; y: number };
  isVisible: boolean;
  onClose: () => void;
  onAction: (action: string, device: NetworkDevice) => void;
}

export const DeviceContextMenu: React.FC<ContextMenuProps> = ({
  device,
  position,
  isVisible,
  onClose,
  onAction,
}) => {
  const menuRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        onClose();
      }
    };

    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        onClose();
      }
    };

    if (isVisible) {
      document.addEventListener('mousedown', handleClickOutside);
      document.addEventListener('keydown', handleEscape);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
      document.removeEventListener('keydown', handleEscape);
    };
  }, [isVisible, onClose]);

  if (!isVisible) return null;

  const menuItems = [
    {
      icon: Activity,
      label: '–ü–∏–Ω–≥ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞',
      action: 'ping',
      color: 'text-green-400 hover:text-green-300',
      disabled: device.status === 'offline',
    },
    {
      icon: Terminal,
      label: 'SSH –ø–æ–¥–∫–ª—é—á–µ–Ω–∏–µ',
      action: 'ssh',
      color: 'text-blue-400 hover:text-blue-300',
      disabled: device.status === 'offline',
    },
    {
      icon: Globe,
      label: '–í–µ–±-–∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å',
      action: 'web',
      color: 'text-purple-400 hover:text-purple-300',
      disabled: device.status === 'offline',
    },
    { divider: true },
    {
      icon: Settings,
      label: '–ù–∞—Å—Ç—Ä–æ–π–∫–∏',
      action: 'settings',
      color: 'text-slate-400 hover:text-white',
    },
    {
      icon: Edit3,
      label: '–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å',
      action: 'edit',
      color: 'text-slate-400 hover:text-white',
    },
    {
      icon: Wifi,
      label: '–°–∫–∞–Ω–∏—Ä–æ–≤–∞—Ç—å –ø–æ—Ä—Ç—ã',
      action: 'scan',
      color: 'text-cyan-400 hover:text-cyan-300',
    },
    { divider: true },
    {
      icon: AlertCircle,
      label: '–ü—Ä–æ—Å–º–æ—Ç—Ä –ª–æ–≥–æ–≤',
      action: 'logs',
      color: 'text-yellow-400 hover:text-yellow-300',
    },
    {
      icon: Shield,
      label: '–ë–µ–∑–æ–ø–∞—Å–Ω–æ—Å—Ç—å',
      action: 'security',
      color: 'text-orange-400 hover:text-orange-300',
    },
    { divider: true },
    {
      icon: Trash2,
      label: '–£–¥–∞–ª–∏—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ',
      action: 'delete',
      color: 'text-red-400 hover:text-red-300',
      dangerous: true,
    },
  ];

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —á—Ç–æ–±—ã –º–µ–Ω—é –Ω–µ –≤—ã—Ö–æ–¥–∏–ª–æ –∑–∞ –≥—Ä–∞–Ω–∏—Ü—ã —ç–∫—Ä–∞–Ω–∞
  const adjustedPosition = {
    x: Math.min(
      position.x,
      (typeof window !== 'undefined' ? window.innerWidth : 1920) - 250
    ),
    y: Math.min(
      position.y,
      (typeof window !== 'undefined' ? window.innerHeight : 1080) -
        menuItems.length * 40
    ),
  };

  return (
    <div
      ref={menuRef}
      className="fixed z-50 min-w-[240px] rounded-lg border border-slate-600/50 bg-slate-800/95 py-2 shadow-xl backdrop-blur-sm"
      style={{
        left: adjustedPosition.x,
        top: adjustedPosition.y,
      }}
    >
      {/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ –º–µ–Ω—é */}
      <div className="border-b border-slate-600/50 px-4 py-2">
        <div className="flex items-center space-x-3">
          <div
            className={`h-3 w-3 rounded-full ${
              device.status === 'online'
                ? 'bg-green-500'
                : device.status === 'warning'
                  ? 'bg-yellow-500'
                  : 'bg-red-500'
            }`}
          />
          <div>
            <div className="text-sm font-medium text-white">{device.name}</div>
            <div className="text-xs text-slate-400">{device.ip}</div>
          </div>
        </div>
      </div>

      {/* –≠–ª–µ–º–µ–Ω—Ç—ã –º–µ–Ω—é */}
      <div className="py-1">
        {menuItems.map((item, index) => {
          if ('divider' in item) {
            return (
              <div key={index} className="mx-2 my-1 h-px bg-slate-600/30" />
            );
          }

          const Icon = item.icon;
          return (
            <button
              key={index}
              onClick={() => {
                onAction(item.action, device);
                onClose();
              }}
              disabled={item.disabled}
              className={`flex w-full items-center space-x-3 px-4 py-2 text-sm transition-colors ${
                item.disabled
                  ? 'cursor-not-allowed text-slate-500'
                  : `${item.color} cursor-pointer hover:bg-slate-700/50`
              } ${item.dangerous ? 'hover:bg-red-500/10' : ''}`}
            >
              <Icon className="h-4 w-4" />
              <span>{item.label}</span>
              {item.disabled && (
                <span className="ml-auto text-xs text-slate-500">(–æ—Ñ–ª–∞–π–Ω)</span>
              )}
            </button>
          );
        })}
      </div>

      {/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–± —É—Å—Ç—Ä–æ–π—Å—Ç–≤–µ */}
      <div className="border-t border-slate-600/50 px-4 py-2 text-xs text-slate-400">
        <div className="flex justify-between">
          <span>–¢–∏–ø: {device.type}</span>
          <span>–í—Ä–µ–º—è –æ—Ç–∫–ª–∏–∫–∞: {device.responseTime}ms</span>
        </div>
        <div className="mt-1 flex justify-between">
          <span>Uptime: {device.uptime.toFixed(1)}%</span>
          <span>
            –ü–æ—Å–ª–µ–¥–Ω–∏–π: {new Date(device.lastSeen).toLocaleTimeString()}
          </span>
        </div>
      </div>
    </div>
  );
};
