import { create } from 'zustand';
import { subscribeWithSelector } from 'zustand/middleware';
import { persist } from 'zustand/middleware';
import {
  NetworkDevice,
  DeviceFolder,
  NetworkMetrics,
  BandwidthData,
  AlertMessage,
  SystemHealth,
  NetworkConnection,
  MonitoringConfig,
} from '../types/schemas';
import {
  mockDevices,
  mockFolders,
  mockAlerts,
  initialMetrics,
  initialSystemHealth,
  initialBandwidthHistory,
  initialConfig,
} from '../data/mock-data';
import api from '../lib/api';

interface NetworkStore {
  // State
  devices: NetworkDevice[];
  folders: DeviceFolder[];
  metrics: NetworkMetrics;
  bandwidthHistory: BandwidthData[];
  alerts: AlertMessage[];
  systemHealth: SystemHealth;
  connections: NetworkConnection[];
  config: MonitoringConfig;

  // UI State
  selectedFolderId: string;
  sidebarCollapsed: boolean;
  loading: boolean;
  error: string | null;
  rootFolderExpanded: boolean;
  othersExpanded: boolean;

  // API integration flags
  useApi: boolean;
  apiConnected: boolean;

  // Device actions
  addDevice: (
    device: Omit<NetworkDevice, 'id' | 'lastSeen' | 'status'>
  ) => Promise<void>;
  updateDevice: (
    deviceId: string,
    updates: Partial<NetworkDevice>
  ) => Promise<void>;
  deleteDevice: (deviceId: string) => Promise<void>;
  pingDevice: (deviceId: string) => Promise<void>;

  // Folder actions
  addFolder: (folder: Omit<DeviceFolder, 'id' | 'children'>) => Promise<void>;
  updateFolder: (
    folderId: string,
    updates: Partial<DeviceFolder>
  ) => Promise<void>;
  deleteFolder: (folderId: string) => Promise<void>;

  // Alert actions
  addAlert: (alert: Omit<AlertMessage, 'id' | 'timestamp'>) => void;
  markAlertResolved: (alertId: string) => void;
  acknowledgeAlert: (alertId: string, userId: string) => void;
  clearAlerts: () => void;

  // Data actions
  setBandwidthData: (data: BandwidthData[]) => void;
  addBandwidthData: (data: BandwidthData) => void;
  updateSystemHealth: (health: SystemHealth) => void;
  updateMetrics: (metrics: Partial<NetworkMetrics>) => void;
  loadDevices: () => Promise<void>;
  loadSystemHealth: () => Promise<void>;
  loadBandwidthData: () => Promise<void>;
  loadNetworkMetrics: () => Promise<void>;

  // UI actions
  setSelectedFolder: (folderId: string) => void;
  setSidebarCollapsed: (collapsed: boolean) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  setUseApi: (useApi: boolean) => void;

  // Data management
  refreshData: () => Promise<void>;
  exportData: () => string;
  importData: (jsonData: string) => boolean;
  resetData: () => void;
  resetToMockData: () => void;
  setRootFolderExpanded: (expanded: boolean) => void;
  setOthersExpanded: (expanded: boolean) => void;
  initializeApi: () => Promise<void>;
}

export const useNetworkStore = create<NetworkStore>()(
  subscribeWithSelector(
    persist(
      (set, get) => ({
        // Initial state with mock data
        devices: mockDevices,
        folders: mockFolders,
        metrics: initialMetrics,
        bandwidthHistory: initialBandwidthHistory,
        alerts: mockAlerts,
        systemHealth: initialSystemHealth,
        connections: [],
        config: initialConfig,

        // UI state
        selectedFolderId: 'root',
        sidebarCollapsed: false,
        loading: false,
        error: null,
        rootFolderExpanded: true,
        othersExpanded: false,

        // API integration flags
        useApi: false,
        apiConnected: false,

        // Device actions
        addDevice: async (deviceData) => {
          const { useApi } = get();

          if (useApi && get().apiConnected) {
            try {
              // –ü—ã—Ç–∞–µ–º—Å—è —Å–æ–∑–¥–∞—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —á–µ—Ä–µ–∑ API
              const response = await api.devices.createDevice(deviceData);
              console.log('‚úÖ –£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —Å–æ–∑–¥–∞–Ω–æ —á–µ—Ä–µ–∑ API:', response);

              // –ü–µ—Ä–µ–∑–∞–≥—Ä—É–∂–∞–µ–º —Å–ø–∏—Å–æ–∫ —É—Å—Ç—Ä–æ–π—Å—Ç–≤ –ø–æ—Å–ª–µ —Å–æ–∑–¥–∞–Ω–∏—è
              await get().loadDevices();
            } catch (error) {
              console.error('‚ùå –û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —á–µ—Ä–µ–∑ API:', error);

              // Fallback –Ω–∞ –ª–æ–∫–∞–ª—å–Ω–æ–µ —Å–æ–∑–¥–∞–Ω–∏–µ
              const device: NetworkDevice = {
                ...deviceData,
                id: Math.random().toString(36).substr(2, 9),
                lastSeen: new Date(),
                status: 'online',
              };

              set((state) => ({
                devices: [...state.devices, device],
                error: '–£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —Å–æ–∑–¥–∞–Ω–æ –ª–æ–∫–∞–ª—å–Ω–æ (API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω)',
              }));
              get().updateMetrics({});
            }
          } else {
            // –õ–æ–∫–∞–ª—å–Ω–æ–µ —Å–æ–∑–¥–∞–Ω–∏–µ (–¥–µ–º–æ —Ä–µ–∂–∏–º)
            const device: NetworkDevice = {
              ...deviceData,
              id: Math.random().toString(36).substr(2, 9),
              lastSeen: new Date(),
              status: 'online',
            };

            set((state) => ({
              devices: [...state.devices, device],
            }));
            get().updateMetrics({});
          }
        },

        updateDevice: async (deviceId, updates) => {
          const { useApi } = get();

          if (useApi && get().apiConnected) {
            try {
              // –ü—ã—Ç–∞–µ–º—Å—è –æ–±–Ω–æ–≤–∏—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —á–µ—Ä–µ–∑ API
              const response = await api.devices.updateDevice(
                deviceId,
                updates
              );
              console.log('‚úÖ –£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –æ–±–Ω–æ–≤–ª–µ–Ω–æ —á–µ—Ä–µ–∑ API:', response);

              // –ü–µ—Ä–µ–∑–∞–≥—Ä—É–∂–∞–µ–º —Å–ø–∏—Å–æ–∫ —É—Å—Ç—Ä–æ–π—Å—Ç–≤ –ø–æ—Å–ª–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è
              await get().loadDevices();
            } catch (error) {
              console.error(
                '‚ùå –û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —á–µ—Ä–µ–∑ API:',
                error
              );

              // Fallback –Ω–∞ –ª–æ–∫–∞–ª—å–Ω–æ–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ
              set((state) => ({
                devices: state.devices.map((device) =>
                  device.id === deviceId
                    ? { ...device, ...updates, lastSeen: new Date() }
                    : device
                ),
                error: '–£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –æ–±–Ω–æ–≤–ª–µ–Ω–æ –ª–æ–∫–∞–ª—å–Ω–æ (API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω)',
              }));
              get().updateMetrics({});
            }
          } else {
            // –õ–æ–∫–∞–ª—å–Ω–æ–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ (–¥–µ–º–æ —Ä–µ–∂–∏–º)
            set((state) => ({
              devices: state.devices.map((device) =>
                device.id === deviceId
                  ? { ...device, ...updates, lastSeen: new Date() }
                  : device
              ),
            }));
            get().updateMetrics({});
          }
        },

        deleteDevice: async (deviceId) => {
          const { useApi } = get();

          if (useApi && get().apiConnected) {
            try {
              // –ü—ã—Ç–∞–µ–º—Å—è —É–¥–∞–ª–∏—Ç—å —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —á–µ—Ä–µ–∑ API
              await api.devices.deleteDevice(deviceId);
              console.log('‚úÖ –£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —É–¥–∞–ª–µ–Ω–æ —á–µ—Ä–µ–∑ API');

              // –ü–µ—Ä–µ–∑–∞–≥—Ä—É–∂–∞–µ–º —Å–ø–∏—Å–æ–∫ —É—Å—Ç—Ä–æ–π—Å—Ç–≤ –ø–æ—Å–ª–µ —É–¥–∞–ª–µ–Ω–∏—è
              await get().loadDevices();
            } catch (error) {
              console.error('‚ùå –û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ —á–µ—Ä–µ–∑ API:', error);

              // Fallback –Ω–∞ –ª–æ–∫–∞–ª—å–Ω–æ–µ —É–¥–∞–ª–µ–Ω–∏–µ
              set((state) => ({
                devices: state.devices.filter(
                  (device) => device.id !== deviceId
                ),
                error: '–£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —É–¥–∞–ª–µ–Ω–æ –ª–æ–∫–∞–ª—å–Ω–æ (API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω)',
              }));
              get().updateMetrics({});
            }
          } else {
            // –õ–æ–∫–∞–ª—å–Ω–æ–µ —É–¥–∞–ª–µ–Ω–∏–µ (–¥–µ–º–æ —Ä–µ–∂–∏–º)
            set((state) => ({
              devices: state.devices.filter((device) => device.id !== deviceId),
            }));
            get().updateMetrics({});
          }
        },

        pingDevice: async (deviceId) => {
          const { useApi } = get();

          if (useApi && get().apiConnected) {
            try {
              // –†–µ–∞–ª—å–Ω—ã–π ping —á–µ—Ä–µ–∑ API
              const response = await api.devices.pingDevice(deviceId);
              console.log('üèì Ping –≤—ã–ø–æ–ª–Ω–µ–Ω —á–µ—Ä–µ–∑ API:', response);

              // –û–±–Ω–æ–≤–ª—è–µ–º —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ —Å —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º–∏ ping
              if (response && response.data) {
                const pingData = response.data as any; // –í—Ä–µ–º–µ–Ω–Ω—ã–π type assertion
                const alive = pingData.alive;
                const responseTime = pingData.responseTime || 0;

                await get().updateDevice(deviceId, {
                  status: alive ? 'online' : 'offline',
                  responseTime: responseTime,
                  lastSeen: new Date(),
                });
              }
            } catch (error) {
              console.error('‚ùå –û—à–∏–±–∫–∞ ping —á–µ—Ä–µ–∑ API:', error);

              // Fallback –Ω–∞ —Å–∏–º—É–ª—è—Ü–∏—é ping
              const device = get().devices.find((d) => d.id === deviceId);
              if (device) {
                const simulatedResponseTime = Math.random() * 50 + 1;
                await get().updateDevice(deviceId, {
                  status: 'online',
                  responseTime: Math.round(simulatedResponseTime),
                  lastSeen: new Date(),
                });
              }
            }
          } else {
            // –°–∏–º—É–ª—è—Ü–∏—è ping (–¥–µ–º–æ —Ä–µ–∂–∏–º)
            console.log(`üèì –°–∏–º—É–ª—è—Ü–∏—è ping —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞: ${deviceId}`);
            await new Promise((resolve) => setTimeout(resolve, 500));

            const device = get().devices.find((d) => d.id === deviceId);
            if (device) {
              const simulatedResponseTime = Math.random() * 50 + 1;
              await get().updateDevice(deviceId, {
                status: 'online',
                responseTime: Math.round(simulatedResponseTime),
                lastSeen: new Date(),
              });
            }
          }
        },

        // Folder actions
        addFolder: async (folderData) => {
          const folder: DeviceFolder = {
            ...folderData,
            id: Math.random().toString(36).substr(2, 9),
            children: [],
          };

          const addFolderRecursive = (
            folders: DeviceFolder[]
          ): DeviceFolder[] => {
            return folders.map((f) =>
              f.id === (folder.parentId || 'root')
                ? { ...f, children: [...f.children, folder] }
                : { ...f, children: addFolderRecursive(f.children) }
            );
          };

          if (!folder.parentId || folder.parentId === 'root') {
            set((state) => ({
              folders: [...state.folders, folder],
            }));
          } else {
            set((state) => ({
              folders: addFolderRecursive(state.folders),
            }));
          }
        },

        updateFolder: async (folderId, updates) => {
          // –°–ø–µ—Ü–∏–∞–ª—å–Ω–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ –¥–ª—è –∫–æ—Ä–Ω–µ–≤–æ–π –ø–∞–ø–∫–∏
          if (folderId === 'root') {
            set((state) => ({
              rootFolderExpanded:
                updates.expanded !== undefined
                  ? updates.expanded
                  : state.rootFolderExpanded,
            }));
            return;
          }

          // –°–ø–µ—Ü–∏–∞–ª—å–Ω–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ –¥–ª—è –ø–∞–ø–∫–∏ "–ò–Ω—ã–µ"
          if (folderId === 'others') {
            set((state) => ({
              othersExpanded:
                updates.expanded !== undefined
                  ? updates.expanded
                  : state.othersExpanded,
            }));
            return;
          }

          const updateFolderRecursive = (
            folders: DeviceFolder[]
          ): DeviceFolder[] => {
            return folders.map((folder) =>
              folder.id === folderId
                ? { ...folder, ...updates }
                : {
                    ...folder,
                    children: updateFolderRecursive(folder.children),
                  }
            );
          };

          set((state) => ({
            folders: updateFolderRecursive(state.folders),
          }));
        },

        deleteFolder: async (folderId) => {
          const removeFolderRecursive = (
            folders: DeviceFolder[]
          ): DeviceFolder[] => {
            return folders
              .filter((folder) => folder.id !== folderId)
              .map((folder) => ({
                ...folder,
                children: removeFolderRecursive(folder.children),
              }));
          };

          set((state) => ({
            folders: removeFolderRecursive(state.folders),
            devices: state.devices.map((device) =>
              device.folderId === folderId
                ? { ...device, folderId: 'root' }
                : device
            ),
          }));
        },

        // Alert actions
        addAlert: (alertData) => {
          const alert: AlertMessage = {
            ...alertData,
            id: Math.random().toString(36).substr(2, 9),
            timestamp: new Date(),
          };

          set((state) => ({
            alerts: [alert, ...state.alerts].slice(0, 50), // Keep only last 50 alerts
          }));
        },

        markAlertResolved: (alertId) => {
          set((state) => ({
            alerts: state.alerts.map((alert) =>
              alert.id === alertId ? { ...alert, resolved: true } : alert
            ),
          }));
        },

        acknowledgeAlert: (alertId, userId) => {
          set((state) => ({
            alerts: state.alerts.map((alert) =>
              alert.id === alertId
                ? {
                    ...alert,
                    acknowledgedBy: userId,
                    acknowledgedAt: new Date(),
                  }
                : alert
            ),
          }));
        },

        clearAlerts: () => {
          set({ alerts: [] });
        },

        // Data actions
        setBandwidthData: (data) => {
          set({ bandwidthHistory: data });
        },

        addBandwidthData: (data) => {
          set((state) => ({
            bandwidthHistory: [...state.bandwidthHistory.slice(-19), data],
          }));
        },

        updateSystemHealth: (health) => {
          set({ systemHealth: health });
        },

        updateMetrics: (metricsUpdate) => {
          const state = get();
          const devices = state.devices;

          const totalDevices = devices.length;
          const onlineDevices = devices.filter(
            (d) => d.status === 'online'
          ).length;
          const offlineDevices = devices.filter(
            (d) => d.status === 'offline'
          ).length;
          const warningDevices = devices.filter(
            (d) => d.status === 'warning'
          ).length;
          const averageResponseTime =
            totalDevices > 0
              ? Math.round(
                  devices.reduce((sum, d) => sum + d.responseTime, 0) /
                    totalDevices
                )
              : 0;

          set({
            metrics: {
              ...state.metrics,
              totalDevices,
              onlineDevices,
              offlineDevices,
              warningDevices,
              averageResponseTime,
              lastUpdate: new Date(),
              ...metricsUpdate,
            },
          });
        },

        loadDevices: async () => {
          set({ loading: true, error: null });
          try {
            const response = await api.devices.getDevices();
            console.log('‚úÖ API Response devices:', response);

            // –ü—Ä–æ–≤–µ—Ä—è–µ–º —Ñ–æ—Ä–º–∞—Ç –æ—Ç–≤–µ—Ç–∞ API
            let devices = [];
            if (response && response.data) {
              // –ï—Å–ª–∏ –æ—Ç–≤–µ—Ç –≤ —Ñ–æ—Ä–º–∞—Ç–µ { data: [], success: true }
              devices = Array.isArray(response.data) ? response.data : [];
            } else if (Array.isArray(response)) {
              // –ï—Å–ª–∏ –æ—Ç–≤–µ—Ç - —ç—Ç–æ –ø—Ä–æ—Å—Ç–æ –º–∞—Å—Å–∏–≤ —É—Å—Ç—Ä–æ–π—Å—Ç–≤
              devices = response;
            } else {
              console.warn('–ù–µ–æ–∂–∏–¥–∞–Ω–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –æ—Ç–≤–µ—Ç–∞ API, –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–∏');
              devices = mockDevices;
            }

            console.log('üì± –ó–∞–≥—Ä—É–∂–µ–Ω–æ —É—Å—Ç—Ä–æ–π—Å—Ç–≤:', devices.length);
            set({
              devices: devices.length > 0 ? devices : mockDevices,
              loading: false,
              apiConnected: true,
              error: null,
            });
            get().updateMetrics({});
          } catch (error) {
            console.error(
              '‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω, –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–æ–≤—ã–µ –¥–∞–Ω–Ω—ã–µ:',
              error
            );
            set({
              devices: mockDevices,
              loading: false,
              error: 'API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω, –ø–æ–∫–∞–∑–∞–Ω—ã –¥–µ–º–æ-–¥–∞–Ω–Ω—ã–µ',
              apiConnected: false,
            });
            get().updateMetrics({});
          }
        },

        loadSystemHealth: async () => {
          try {
            const response = await api.metrics.getSystemHealth();
            console.log('‚úÖ API Response system health:', response);

            // –ü—Ä–æ—Å—Ç–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ - –ø–æ–∫–∞ –∏—Å–ø–æ–ª—å–∑—É–µ–º initialSystemHealth –∏ –æ–±–Ω–æ–≤–ª—è–µ–º apiConnected
            set({
              systemHealth: {
                ...initialSystemHealth,
                lastUpdate: new Date(),
              },
              apiConnected: true,
              error: null,
            });

            console.log('üíª –°–∏—Å—Ç–µ–º–Ω—ã–µ –º–µ—Ç—Ä–∏–∫–∏ –æ–±–Ω–æ–≤–ª–µ–Ω—ã (API –ø–æ–¥–∫–ª—é—á–µ–Ω)');
          } catch (error) {
            console.error('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è —Å–∏—Å—Ç–µ–º–Ω–æ–≥–æ –∑–¥–æ—Ä–æ–≤—å—è:', error);
            set({
              systemHealth: initialSystemHealth,
              apiConnected: false,
              error: '–ú–µ—Ç—Ä–∏–∫–∏ —Å–∏—Å—Ç–µ–º—ã –Ω–µ–¥–æ—Å—Ç—É–ø–Ω—ã',
            });
          }
        },

        loadBandwidthData: async () => {
          try {
            const response = await api.metrics.getBandwidthHistory();
            console.log('‚úÖ API Response bandwidth:', response);

            // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –¥–∞–Ω–Ω—ã–µ –ø—Ä–æ–ø—É—Å–∫–Ω–æ–π —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç–∏
            let bandwidthData = initialBandwidthHistory;
            if (response && response.data && Array.isArray(response.data)) {
              bandwidthData = response.data;
            }

            set({
              bandwidthHistory: bandwidthData,
              apiConnected: true,
            });
            console.log('üìä –î–∞–Ω–Ω—ã–µ –ø—Ä–æ–ø—É—Å–∫–Ω–æ–π —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç–∏ –æ–±–Ω–æ–≤–ª–µ–Ω—ã');
          } catch (error) {
            console.error(
              '‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è –¥–∞–Ω–Ω—ã—Ö –ø—Ä–æ–ø—É—Å–∫–Ω–æ–π —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç–∏:',
              error
            );
            set({
              bandwidthHistory: initialBandwidthHistory,
              apiConnected: false,
            });
          }
        },

        loadNetworkMetrics: async () => {
          try {
            const response = await api.metrics.getNetworkMetrics();
            console.log('‚úÖ API Response network metrics:', response);

            // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –º–µ—Ç—Ä–∏–∫–∏ —Å–µ—Ç–∏
            let metrics = initialMetrics;
            if (response && response.data) {
              metrics = {
                ...initialMetrics,
                ...response.data,
                lastUpdate: new Date(),
              };
            }

            set({
              metrics,
              apiConnected: true,
            });
            console.log('üåê –ú–µ—Ç—Ä–∏–∫–∏ —Å–µ—Ç–∏ –æ–±–Ω–æ–≤–ª–µ–Ω—ã');
          } catch (error) {
            console.error('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω –¥–ª—è –º–µ—Ç—Ä–∏–∫ —Å–µ—Ç–∏:', error);
            set({
              metrics: initialMetrics,
              apiConnected: false,
            });
          }
        },

        // UI actions
        setSelectedFolder: (folderId) => {
          set({ selectedFolderId: folderId });
        },

        setSidebarCollapsed: (collapsed) => {
          set({ sidebarCollapsed: collapsed });
        },

        setLoading: (loading) => {
          set({ loading });
        },

        setError: (error) => {
          set({ error });
        },

        setUseApi: (useApi) => {
          set({ useApi });
        },

        // Data management
        refreshData: async () => {
          set({ loading: true, error: null });
          try {
            const { useApi } = get();
            if (useApi) {
              // –ü—Ä–æ–±—É–µ–º –∑–∞–≥—Ä—É–∑–∏—Ç—å –¥–∞–Ω–Ω—ã–µ —Å API
              await Promise.allSettled([
                get().loadDevices(),
                get().loadSystemHealth(),
                get().loadBandwidthData(),
                get().loadNetworkMetrics(),
              ]);
            } else {
              // –ò—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–æ–≤—ã–µ –¥–∞–Ω–Ω—ã–µ
              await new Promise((resolve) => setTimeout(resolve, 500));
              get().updateMetrics({});
            }
            set({ loading: false });
          } catch (error) {
            set({
              loading: false,
              error:
                error instanceof Error
                  ? error.message
                  : '–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö',
            });
          }
        },

        // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è API –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è
        initializeApi: async () => {
          console.log('üîå –ü–æ–ø—ã—Ç–∫–∞ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è –∫ API...');
          try {
            // –ü—Ä–æ–≤–µ—Ä—è–µ–º –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç—å API
            const response = await fetch('http://localhost:5000/api/health');
            if (response.ok) {
              console.log('‚úÖ API –¥–æ—Å—Ç—É–ø–µ–Ω! –í–∫–ª—é—á–∞–µ–º API —Ä–µ–∂–∏–º');
              set({ useApi: true, apiConnected: true });
              await get().refreshData();
            } else {
              console.log('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω, –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–æ–≤—ã–µ –¥–∞–Ω–Ω—ã–µ');
              set({ useApi: false, apiConnected: false });
            }
          } catch (error) {
            console.log('‚ùå API –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω, –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–æ–∫–æ–≤—ã–µ –¥–∞–Ω–Ω—ã–µ');
            set({ useApi: false, apiConnected: false });
          }
        },

        exportData: () => {
          const state = get();
          const exportData = {
            devices: state.devices,
            folders: state.folders,
            connections: state.connections,
            config: state.config,
            exportTimestamp: new Date().toISOString(),
          };
          return JSON.stringify(exportData, null, 2);
        },

        importData: (jsonData) => {
          try {
            const data = JSON.parse(jsonData);
            set({
              devices: data.devices || [],
              folders: data.folders || [],
              connections: data.connections || [],
              config: data.config || initialConfig,
            });
            get().updateMetrics({});
            return true;
          } catch {
            return false;
          }
        },

        resetData: () => {
          set({
            devices: [],
            folders: [],
            metrics: initialMetrics,
            bandwidthHistory: [],
            alerts: [],
            systemHealth: initialSystemHealth,
            connections: [],
            selectedFolderId: 'root',
            loading: false,
            error: null,
          });
        },

        resetToMockData: () => {
          set({
            devices: mockDevices,
            folders: mockFolders,
            metrics: initialMetrics,
            bandwidthHistory: initialBandwidthHistory,
            alerts: mockAlerts,
            systemHealth: initialSystemHealth,
            connections: [],
            selectedFolderId: 'root',
            loading: false,
            error: null,
            rootFolderExpanded: true,
            othersExpanded: false,
          });
          get().updateMetrics({});
        },

        setRootFolderExpanded: (expanded) => {
          set({ rootFolderExpanded: expanded });
        },

        setOthersExpanded: (expanded) => {
          set({ othersExpanded: expanded });
        },
      }),
      {
        name: 'network-monitor-storage',
        partialize: (state) => ({
          devices: state.devices,
          folders: state.folders,
          config: state.config,
          selectedFolderId: state.selectedFolderId,
          sidebarCollapsed: state.sidebarCollapsed,
        }),
      }
    )
  )
);
